  left:0%;
  height:auto;
  width:255px;
  z-index: -1;
}
.leftimages{
  position: absolute;
  top:45%;
  left:85%;
  height:auto;
  width:255px;
  z-index: -1;

}
.element
{
  text-shadow: 0px 0px 7px rgba(0, 0, 0, 0.75);
  color: #0080ff;
  font-size: 50px;
  position: absolute;
  top:2%;
